.header-content {
  width: 100%;
  height: 100%;
  padding: 0 50px 50px 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 1040px) {
  .header-content {
    align-items: flex-start;
  }
}

@media only screen and (max-width: 767px) {
  .header-content {
    padding: 0 20px 50px;
  }
}

.header-menu {
  margin-bottom: 20px;
}

@media only screen and (max-width: 1040px) {
  .header-menu {
    margin-left: -50px;
    position: absolute;
    transform: translateY(-100%);
    background-color: #000;
    overflow: hidden;
    transition: transform 0.5s cubic-bezier(0.55, 0.055, 0.675, 0.19);
    will-change: transform;
  }
}

@media only screen and (max-width: 767px) {
  .header-menu {
    margin-left: -20px;
  }
}

@media only screen and (max-width: 1040px) {
  .header-menu-active {
    transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translateY(0);
  }
}

@media only screen and (max-width: 1040px) {
  .menu-list-wrapper {
    transition: transform 0.5s cubic-bezier(0.55, 0.055, 0.675, 0.19);
    transform: translateY(100%);
  }
}

@media only screen and (max-width: 1040px) {
  .header-menu-active > .menu-list-wrapper {
    transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translateY(0);
  }
}

.header-menu-list {
  display: flex;
  width: 100%;
  overflow: hidden;
}

@media only screen and (max-width: 1040px) {
  .header-menu-list {
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    padding-top: 120px;
    padding-left: calc(50px + 9vw);
    padding-right: 30px;
  }
  .header-menu-list > li > a {
    margin: 0;
    margin-bottom: 20px;
    color: #fff;
  }
  .header-menu-list > li > a:hover {
    color: #ff3500;
  }
}

@media only screen and (max-width: 768px) {
  .header-menu-list {
    padding-left: calc(20px + 9vw);
  }
}

@media only screen and (max-width: 1040px) {
  .header-menu-active .header-menu-list {
    display: block;
    text-align: left;
  }
}

.header-menu-list > li > a {
  margin: 0px 15px 20px 15px;
  font-size: 16px;
  color: white;
  text-decoration: none;
  border-bottom: 2px solid #fff4;
  display: inline-block;
  animation: moveInMenuAnimation 1s cubic-bezier(0.215, 0.61, 0.355, 1) backwards;
}

@media only screen and (max-width: 1040px) {
  .header-menu-list > li > a {
    display: block;
    transform: translateY(-100px);
    opacity: 0.01;
    transition: transform 0.5s cubic-bezier(0.55, 0.055, 0.675, 0.19), opacity 0.5s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
}

.header-menu-active .header-menu-list > li > a {
  transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1), opacity 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
  transform: translateY(0);
  opacity: 1;
}

.header-menu-list > li:nth-of-type(1) > a {
  animation-delay: 0.1s;
}

.header-menu-list > li:nth-of-type(2) > a {
  animation-delay: 0.2s;
}

.header-menu-list > li:nth-of-type(3) > a {
  animation-delay: 0.3s;
}

.header-menu-list > li:nth-of-type(4) > a {
  animation-delay: 0.4s;
}

.header-menu-list > li:nth-of-type(5) > a {
  animation-delay: 0.5s;
}

.header-menu-list > li:nth-of-type(6) > a {
  animation-delay: 0.6s;
}

.header-menu-list > li:nth-of-type(7) > a {
  animation-delay: 0.7s;
}

.header-menu-list > li:nth-of-type(8) > a {
  animation-delay: 0.8s;
}

.header-menu-list > li:nth-of-type(8) > a {
  transition-delay: 15ms;
}

.header-menu-list > li:nth-of-type(7) > a {
  transition-delay: 30ms;
}

.header-menu-list > li:nth-of-type(6) > a {
  transition-delay: 45ms;
}

.header-menu-list > li:nth-of-type(5) > a {
  transition-delay: 60ms;
}

.header-menu-list > li:nth-of-type(4) > a {
  transition-delay: 75ms;
}

.header-menu-list > li:nth-of-type(3) > a {
  transition-delay: 90ms;
}

.header-menu-list > li:nth-of-type(2) > a {
  transition-delay: 105ms;
}

.header-menu-list > li:nth-of-type(1) > a {
  transition-delay: 120ms;
}

.header-menu-list > li > a:hover {
  color: red;
  border-bottom: 2px solid #f006;
}

.header-menu-active .header-menu-list > li:nth-of-type(1) > a {
  animation-delay: 0.1s;
  transition-delay: 15ms;
}

.header-menu-active .header-menu-list > li:nth-of-type(2) > a {
  animation-delay: 0.2s;
  transition-delay: 30ms;
}

.header-menu-active .header-menu-list > li:nth-of-type(3) > a {
  animation-delay: 0.3s;
  transition-delay: 45ms;
}

.header-menu-active .header-menu-list > li:nth-of-type(4) > a {
  animation-delay: 0.4s;
  transition-delay: 60ms;
}

.header-menu-active .header-menu-list > li:nth-of-type(5) > a {
  animation-delay: 0.5s;
  transition-delay: 75ms;
}

.header-menu-active .header-menu-list > li:nth-of-type(6) > a {
  animation-delay: 0.6s;
  transition-delay: 90ms;
}

.header-menu-active .header-menu-list > li:nth-of-type(7) > a {
  animation-delay: 0.7s;
  transition-delay: 105ms;
}

.header-menu-active .header-menu-list > li:nth-of-type(8) > a {
  animation-delay: 0.8s;
  transition-delay: 120ms;
}

.header-menu-list > li > a:hover {
  color: red;
  border-bottom: 2px solid #f006;
}

