.hamburger {
  display: none;
  width: 35px;
  height: 50;
  position: absolute;
  top: 30px;
  right: 25px;
  z-index: 10;
  cursor: pointer;
}

@media only screen and (max-width: 1040px) {
  .hamburger {
    display: block;
  }
}

.hamburger:hover .hamburger-item {
  width: 100%;
}

.hamburger-item {
  height: 3px;
  background-color: #ffffff;
  display: block;
  margin-bottom: 8px;
  transition: all 250ms cubic-bezier(0.215, 0.61, 0.355, 1);
}

.hamburger-item:first-child {
  width: calc(100% * (5 / 6));
}

.hamburger-item:nth-child(2) {
  width: 100%;
}

.hamburger-item:last-child {
  width: calc(100% * (4 / 6));
}

.hamburger-active {
  z-index: 5;
}

.hamburger-active .hamburger-item:first-child {
  width: 100%;
  transform: translate(0, 11px) rotate(45deg);
}

.hamburger-active .hamburger-item:nth-child(2) {
  opacity: 0;
}

.hamburger-active .hamburger-item:last-child {
  width: 100%;
  transform: translate(0, -11px) rotate(-45deg);
}

@keyframes logoAimation {
  from {
    transform: scale(0.5);
  }
  to {
    transform: scale(1);
  }
}

@keyframes moveInAnimation {
  from {
    transform: translateY(27px);
  }
  to {
    transform: translateY(0px);
  }
}

@keyframes moveInMenuAnimation {
  from {
    opacity: 0;
    transform: translateY(57px);
