  to {
    transform: translateY(0px);
  }
}

@keyframes moveInButtonAnimation {
  from {
    opacity: 0;
    transform: translateY(57px);
  }
  to {
    transform: translateY(0px);
  }
}

@keyframes moveInButtomAnimation {
  from {
    opacity: 0;
    transform: translateY(57px);
  }
  to {
    transform: translateY(0px);
  }
}

