.contacts-wrapper {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)), url(../../img/header-bg-light.jpg);
  background-size: cover, cover; }



.contacts {
	font-weight: bold;
	padding-top: 55px;
	padding-bottom: 85px; }

.contacts__title {
  color: $white;
  @include r(900) {
    font-size: 40px; }
  @include r(400) {
    font-size: 30px; } }

.contacts__text {
  color: $contacts-text;
  font-size: 24px;
  @include r(900) {
    font-size: 20px; } }


.contacts__text, .contacts__title {
    text-align: center; }

.contacts__socials {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 35px;
  align-items: center; }

.contacts__socials-img {
  width: 100%;
  padding: 0 10px;

  @include r(420) {
    padding: 0 5px; } }





